@import (less) "color.less";
.menu {
    position: relative;
    width: 100%;
    height: 0;
    overflow-x: hidden;
    overflow-y: hidden;
    background-color: @darkest_gray;
    -moz-transition: height 0.25s ease-out;
    -o-transition: height 0.25s ease-out;
    transition: height 0.25s ease-out;
    .icon {
        margin-right: 0.313em;
        font-size: 1.422em !important;
        vertical-align: middle;
    }
}

.title-operation {
    color: @white;
    font-size: 1.85em;
}

.menu.closed {
    height: 0;
}

// .menu.open {
    // height: 23em;
// }

.main-menu-container {
    padding: 0;
}

.section-menu:nth-child(2n+3) {
    a {
        color: @orange;
        border: 1px solid #FA9600;
        border-radius: 4px;
        &:hover{
            background-color: #FA9600;
            color: #323232;
            border-color: #323233;
        }
    }
    // clear: left;
}

.p-0 {
    padding: 0px !important;
}

.main-menu {
    list-style: none;
    padding: 0 2.187em 0 2.187em;
    margin: 0;
    a {
        color: @light_gray;
        display: table;
        width: 100%;
        padding: 0em 1.250em 0em 1.250em;
        margin: .62em 0 .62em 0;
        text-decoration: none;
        font-weight: bold;
        cursor: pointer;
        height: 2.813em;
        border: 1px solid #6f6f6f;
        border-radius: 4px;
        &:hover, &:focus, &:active {
            background-color: @green;
            color: @white;
            border: 1px solid @green;
        }
        span {
            display: table-cell;
            vertical-align: middle;
            text-align: center;
            text-transform: uppercase;
            font-size: 0.75em;
            &:nth-child(1) {
                width: 1.563em;
            }
        }
    }
    .select {
        background-color: @green;
        color: @white;
        border: 0.0625em solid @green;
    }
}

.upper-bar {
    border-bottom: 0.0625em solid @gray;
}

.menu-principale {
    color: @light_gray;
    font-size: 1em;
    padding: 1em 0.625em 0.625em 0.625em;
    cursor: pointer;
    .icon {
        font-size: 0.875em;
    }
}

.first-level-menu-operation {
    position: absolute;
    left: 0;
    visibility: hidden;
    right: 0;
    opacity: 0;
    left: -6.250em;
    -moz-transition: all 0.35s ease-out;
    -o-transition: all 0.35s ease-out;
    transition: all 0.35s ease-out;
}

.first-level-menu-operation.open {
    opacity: 1;
    left: 0;
    visibility: visible;
    margin-top: 0.625em;
    -moz-transition: all 0.35s  ease-out;
    -o-transition: all 0.35s  ease-out;
    transition: all 0.35s  ease-out;

}

@keyframes example2 {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}


//
@media (min-width: 768px) {
    overflow-y: auto;
}

@media (max-width: 992px) {
    .main-menu {
        a {
            width: 10.375em;
        }
    }
}

:host-context(.contrasto-on) {
    .menu {
        background-color: @black;
        &.open {
            border-color: @white;
        }
    }
    .main-menu {
        a {
            color: @white;
            border-color: @white;
            &:hover, &:focus, &:active {
                background-color: @white;
                color: @black;
                border-color: @white;
            }
        }
        .select {
            background-color: @white;
            color: @black;
            border-color: @white;
        }
    }
    .upper-bar {
        border-color: @white;
    }
    .menu-principale {
        color: @white;
    }
    .selected-first-level-menu-operation {
        color: @white;
    }
}


